import styled from "styled-components";

export const StyledListItem = styled.li`
  display: grid;
  grid-template-columns: 4fr 1fr 1fr;

  text-align: left;
  padding: 15px 20px;

  font-weight: 400;
  font-size: 16px;
  line-height: 19px;

  border-bottom: 2px solid rgb(204, 213, 255);
`;
